export const GET_FILMS_TRENDS = "GET_FILMS_TRENDS"


export function getTrendFilms(page){
    return function (dispatch){
        return fetch(`https://api.themoviedb.org/3/trending/all/week?api_key=1817e292f51d003a69f5fad178683650&page=${page}`).then(response=>response.json()).then(data=> dispatch(listTrendFilms(data)))
}}
export function listTrendFilms(data){
    return{
        type:GET_FILMS_TRENDS,
        payload:data
}
}

export function aaaGet(data){
   return{ 
       type:"BYTE_ADD",
    payload:data
}
}


export function addPage(page){
    return {
        type:"ADD_PAGE",
        payload:page
    }
}

export function downPage(page){
    return {
        type:"DOWN_PAGE",
        payload:page
    }
}